.resolve__wrapper {
  padding: 60px 72px;
}

.resolve__title {
  margin: 0;
  margin-bottom: 74px;

  font-size: $largeFontSize;
  line-height: $largeLineHeight;
  text-transform: uppercase;
  text-align: center;
}

.resolve__list {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  list-style: none;

  & > li {
    display: flex;
    flex-direction: column;
    margin-bottom: 23px;
    margin-right: 18px;
    width: 420px;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  img {
    display: block;

    border-radius: 15px 15px 0 0;
  }
}

.resolve__description {
  position: relative;

  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 28px 25px 42px 21px;

  border-radius: 0 0 15px 15px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.1);

  &::before {
    content: "";
    position: absolute;
    top: 36px;
    left: 16px;

    width: 73px;
    height: 69px;

    background: url("../img/icon-resolve-plus.svg") no-repeat center center;
  }
}

.resolve__head {
  margin: 0 18px 22px 98px;

  font-size: $baseFontSize + 6;
  line-height: $baseLineHeight + 8;
  text-transform: uppercase;
}

.resolve__text {
  margin: 0;
  padding-left: 16px;

  border-left: 5px solid $color-dark-green;
}

.resolve__cost {
  margin-top: auto;
  padding: 0;
  padding-top: 18px;

  font-weight: $boldFontWeight;
  list-style: none;
  color: $color-dark-green;

  span {
    color: $black;
  }
}

.resolve__link {
  margin: 35px auto 0;
  padding: 6px 12px;
  max-width: 153px;

  font-size: $smallFontSize;
  line-height: $smallLineHeight;
  font-weight: $boldFontWeight;
  text-transform: uppercase;
  text-align: center;
  color: $white;

  background: linear-gradient(180deg, $color-green 0%, $color-dark-green 100%);
  border-radius: 5px;
}
