.page-header {
  position: relative;

  padding: 7px 0 6px;

  background-color: $white;
  border-bottom: 1px solid $color-light-gray;
  box-shadow: 0 4px 7px rgba(0, 0, 0, 0.24);

  z-index: 101;
}

.page-header__wrapper {
  display: flex;
  align-items: center;
}

.page-header__phone {
  display: block;
  margin-top: 5px;
}
