.help {
  margin-bottom: 100px;

  .button {
    margin-top: 70px;

    font-size: $baseFontSize + 11;
    line-height: $baseLineHeight + 14;
  }
}

.help__wrapper {
  position: relative;

  margin: 0;
  padding: 0 150px 120px;

  &::before {
    content: "";
    position: absolute;
    top: 74px;
    left: 100px;

    width: 513px;
    height: 644px;

    background: url("../img/help-man.png") no-repeat center center;
  }
}

.help__title {
  margin: 0 0 98px 340px;

  font-size: $largeFontSize - 2;
  line-height: $largeLineHeight - 3;
  text-transform: uppercase;

  span {
    color: $color-orange;
  }
}

.help__description {
  margin-left: auto;
  max-width: 555px;
}

.help__text {
  position: relative;
  margin: 40px 0;
  padding-left: 60px;

  font-size: $baseFontSize + 6;
  line-height: $baseLineHeight + 8;

  &::before {
    content: "";
    position: absolute;
    top: 18px;
    left: -34px;

    width: 76px;
    height: 51px;

    background: url("../img/icon__help-check.svg") no-repeat center center;
  }
}
