.intro {
  position: relative;
  z-index: 100;

  .button {
    margin-top: 5px;
    max-width: 458px;
  }

  &::after {
    content: "";
    position: absolute;
    top: -100px;
    left: 0;

    width: 960px;
    height: 1302px;

    background: url("../img/intro-bg-1.png") no-repeat top left;
  }
}

.intro__wrapper {
  position: relative;

  padding: 90px 150px;

  z-index: 100;

  &::after {
    content: "";
    position: absolute;
    top: 28px;
    right: 142px;

    width: 481px;
    height: 640px;

    background: url("../img/intro-man.png") no-repeat center center;

    z-index: -1;
  }
}

.intro__head {
  margin-bottom: 47px;
  max-width: 578px;

  font-size: $largerFontSize;
  font-weight: $boldFontWeight;
  line-height: $largerLineHeight;
  letter-spacing: 0.035em;
  text-transform: uppercase;
}

.intro__title {
  margin: 0;

  font-size: $largerFontSize;
  color: $color-orange;
}

.intro__text {
  margin: 0 0 26px 6px;
  max-width: 568px;

  color: $color-gray;

  b {
    display: block;
    margin-bottom: -4px;

    font-size: $baseFontSize + 6;
    font-weight: $mediumFontWeight;
    line-height: $baseLineHeight + 8;
    color: $black;
  }

  &--last {
    margin-bottom: 10px;
  }
}

.intro__icon-1 {
  position: absolute;

  &--left {
    top: 0;
    left: -117px;
  }

  &--right {
    top: 40px;
    right: -76px;
  }
}

.intro__icon-2 {
  position: absolute;
  right: 20px;
  bottom: -122px;
}

.intro__icon-3 {
  position: absolute;
  left: 0;
  bottom: -79px;
}

@media (max-width: 1440px) {
  .intro {
    overflow: hidden;
  }
}
