.cases__wrapper {
  padding: 90px 150px;

  text-align: center;
  color: $white;

  background-color: $black;
}

.cases__title {
  margin: 0;
  margin-bottom: 38px;

  font-size: $largerFontSize - 5;
  line-height: $largerLineHeight + 9;
  text-transform: uppercase;
}

.cases__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 0;
  margin-top: 24px;
  padding: 0;

  list-style: none;

  img {
    display: block;
  }
}
