.main-nav {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;

  .nav__item {
    &:not(:last-child) {
      margin-right: 31px;
      margin-bottom: 10px;
    }
    &:last-child {
      margin-left: 28px;

      text-align: right;
    }
  }

  .button {
    padding: 13px 38px;
  }
}
