.form {
  display: flex;
  flex-direction: column;

  input {
    border: none;

    &[type="text"],
    &[type="tel"] {
      margin-bottom: 17px;
      padding: 0 30px;
      width: 585px;
      height: 70px;

      font-weight: $boldFontWeight;

      border-radius: 10px;
    }

    &::placeholder {
      color: rgba(0, 0, 0, 1);
    }

    &[type="submit"] {
      margin-top: 48px;
      margin-left: auto;
      padding: 6px 12px;
      max-width: 293px;

      font-size: $baseFontSize;
      line-height: $baseLineHeight;
      font-weight: $boldFontWeight;
      text-transform: uppercase;
      color: $white;

      background: linear-gradient(180deg, $color-green 0%, $color-dark-green 100%);
      border-radius: 5px;

      cursor: pointer;
    }
  }
}
