html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  min-width: 1440px;
  //min-width: 320px;

  font-family: "Montserrat", "Arial", sans-serif;
  font-size: $baseFontSize;
  line-height: 29px;
  color: $color-base;
}

a {
  color: $color-base;
  text-decoration: none;

  &:hover {
    color: $hover-color;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  border: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
