.about__wrapper {
  position: relative;

  padding: 4px 150px 70px;

  &::before {
    content: "";
    position: absolute;
    top: 42px;
    left: 150px;

    width: 521px;
    height: 605px;

    background: url("../img/about-man.png") no-repeat center center;
  }
}

.about__title {
  margin: 0;

  font-size: $largeFontSize;
  line-height: $largeLineHeight;
  text-transform: uppercase;
}

.about__description {
  margin: 40px 30px 0 auto;
  max-width: 556px;

  img {
    display: block;
    margin-bottom: 24px;
  }

  p {
    margin: 0 0 30px 0;
  }
}

.about__quote {
  margin: -14px 0 13px 0;
  padding-left: 23px;

  font-family: "Arial", sans-serif;
  text-align: justify;

  border-left: 2px solid $color-dark-green;
}

.about__icon-1 {
  position: absolute;
  left: 28px;
  bottom: 120px;

  width: 102px;
}

.about__icon-sign {
  position: absolute;
  left: 112px;
  bottom: -136px;
}
