.reviews {
  .button {
    margin: 12px auto 0;
    max-width: 805px;

    font-size: $baseFontSize + 6;
    line-height: $baseLineHeight + 8;
  }
}

.reviews__wrapper {
  background-color: $color-orange;
}

.reviews__title {
  margin: 0 auto;
  max-width: 250px;

  font-size: $largeFontSize;
  line-height: $largeLineHeight;
  text-transform: uppercase;
  text-align: center;
}

.reviews__list {
  margin: 0;
}

.reviews__item {
  position: relative;

  margin: 10px auto 0;
  padding: 172px 45px 38px;
  width: 1100px;

  border-radius: 25px;
  background-color: $white;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.1);

  p {
    margin: 0;
    margin-top: 20px;

    background-color: $white;
  }
}

.reviews__img {
  position: absolute;
  top: -10px;
  left: 50%;

  margin-left: -85px;
}

.reviews__author {
  font-size: $baseFontSize + 2;
  line-height: $baseLineHeight + 3;
  text-align: center;
  text-transform: uppercase;
}
