.page-footer {
  margin: 0;
  padding: 0;

  font-size: $baseFontSize - 3;
  line-height: $baseLineHeight - 3;
  color: $white;

  background-color: $black;

  a {
    display: block;

    color: $color-yellow;
  }
}

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 80px 150px;
}

.page-footer__nav {
  margin: 18px 0 27px;

  li {
    margin-right: 26px;
  }
}

.page-footer__copyright {
  margin: 27px 0;
}

.page-footer__partner-link {
  margin-top: 27px;
}
