.save__wrapper {
  margin: 0;
  padding: 100px 85px;
}

.save__title {
  margin: 0;
  margin-bottom: 55px;

  font-size: $largeFontSize;
  line-height: $largeLineHeight;
  text-transform: uppercase;
}

.save__list {
  margin: 0;
  padding: 0;

  list-style: none;

  .row {
    display: flex;

    &:last-child {
      margin-top: -100px;
      margin-left: 246px;
    }
  }
}

.save__item {
  position: relative;

  padding: 126px 40px 40px;
  width: 466px;
  height: 531px;

  background: url("../img/save-bg.svg") no-repeat center center;

  //background-color: $color-orange;

  &:first-child {
    margin-right: 30px;
  }
}

.save__head {
  margin: 0;

  font-size: $largeFontSize;
  line-height: $largeLineHeight;
  text-transform: uppercase;
  color: $white;
}

.save__text {
  margin: 0;
}
